<template>
    <div :class="[paramname, {'fixed': flag}]">
        <h3 v-if="flag">{{ parameter }}</h3>
        <h3 v-else>{{ rand }}</h3>
        <div class="param">
            <div class="paramblock"></div>
        </div>
        <h2>{{ paramname }}</h2>
    </div>
</template>

<script>
import {onMounted, ref} from 'vue';
// let rand = ref(0);

export default {
    name: 'ParamBlock',
    setup() {
        const rand = ref(0);
        onMounted(() => {
            setInterval( function () {
                rand.value = Math.floor(Math.random() * 5) + 1;
            },100);
        });
        return {
            rand
        }
    },
    // data() {
    //     return {
    //         rand: 0
    //     }
    // },
    inject: ['fixedFlag','randomParam'],
    props: ['paramname'],
    computed: {
        parameter() {
            if(this.paramname === 'Speed') {
                return this.randomParam[0];
            } else if (this.paramname === 'Scale') {
                return this.randomParam[1];
            } else if (this.paramname === 'Charge') {
                return this.randomParam[2];
            } else if (this.paramname === 'Shield') {
                return this.randomParam[3];
            }
            return 0;
        },
        flag() {
            return this.fixedFlag.value;
        }
    },
    methods:{
        randomint() {
            setInterval( function() {
                this.rand.value = Math.floor(Math.random() * 4) + 1;
            },100);
        }
    },
    
}
</script>

<style lang="scss" scoped>
.param {
    height: 300px;
    width: 100px;
    background-color: #ccc;
    position: relative;
    --param: v-bind(parameter);
}
.paramblock {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: calc(20% * var(--param));
    animation: .8s ease-in-out alternate infinite beyoon;
}
.Speed {
    .paramblock {
        background-color: orange;
    }
    h2 {
        color: orange;
    }
}
.Scale {
    .paramblock {
        background-color: green;
    }
    h2 {
        color: green;
    }
}
.Charge {
    .paramblock {
        background-color: purple;
    }
    h2 {
        color: purple;
    }
}
.Shield {
    .paramblock {
        background-color: blue;
    }
    h2 {
        color: blue;
    }
}
.fixed {
    .paramblock {
        animation-play-state: paused;
        animation: .5s ease-in-out alternate paramfix;
    }
}


@keyframes beyoon {
    0% {
        height: 0;
    }
    100% {
        height: 100%;
    }
}

@keyframes paramfix {
    0% {
        height: 0;
    }
    100% {
        height: calc(20% * var(--param));
    }
}
</style>